
<template>
	<div class='center'>
		<img src="@/assets/small_logo.png" class="logo" alt="">
		<div v-if="page==1">
			<p class="welcome">Help us make Metatel better</p>
			<div class="hr"></div>
			<div class='node'>Network node</div>
			<div class="node_list">
				<div class="serverinfo" v-loading="loading" style="overflow: hidden;margin:auto;padding:20px">
					<div class="item"  v-for="(item,index) in serverinfo" :key="index" :class="{active:i === index}" @click="clickAdd(index,item)" >{{item|server}}</div>
				</div>
			</div>
			<div class="btn_box">
				<el-button type="default" class='btn' round @click="cancel">Cancel</el-button>
				<el-button type="primary" class='btn' round @click="next">Next</el-button>
			</div>
		</div>
		<div v-if="page==2">
			<p class="welcome">Create New Password</p>
			<div class="hr"></div>

			<el-form  :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
				<div class="new_password">New Password</div>
				<el-form-item prop="pass">
					<el-input size="large" show-password auto-complete="new-password" type="password" v-model="ruleForm.pass"></el-input>
				</el-form-item>
				<div class="confirm_password">Confirm Password</div>
				<el-form-item prop="checkPass">
					<el-input size="large" show-password auto-complete="new-password" type="password" v-model="ruleForm.checkPass"></el-input>
				</el-form-item>
				<div class='notice'><img src="@/assets/notice.png" alt=""> We will use this password to encrypt your Metatel address.</div>
				<el-form-item>
					<div class='btn_box'>
						<el-button round class='btn' @click="back">Cancel</el-button>
						<el-button class='btn' type="primary" :loading="buttonloading" @click="submitForm('ruleForm')" round>Create</el-button>
					</div>
				</el-form-item>
			</el-form>
		</div>
		<div v-if="page==3">
			<p class="welcome">Mnemonics and account information</p>
			<div class="hr"></div>
			<div class="info_box">
				<div class='txt'>Mnemonics</div>
				<div class="mnemonics">
					{{epkForm.word}}
					<div class='copy' title='Copy' :data-clipboard-text="epkForm.word" style="top:8px"></div>
				</div>
				<div class="tishi">Usually 12 words (sometimes 24) are separated by a single space.</div>
			</div>
			<div class="info_box">
				<div class='txt'>Account</div>
				<div class="account">
					{{epkForm.cid}}
					<div class='copy' title='Copy' :data-clipboard-text="epkForm.cid"></div>
				</div>
			</div>
			<div class="btn_box">
				<el-button class='btn' round @click="back">Back</el-button>
				<el-button class='btn' type="primary" @click="buyrtc()" round>QR code</el-button>
			</div>
		</div>
		<div v-if="page==4">
			<p class="welcome">Login QR code</p>
			<div class="hr"></div>
			<div id="qrcode" class='qrcode' ref="qrcode"></div>
			<div class='notice' style="text-align:center;margin:auto;"><img src="@/assets/notice.png" alt=""> We will use this password to encrypt your Metatel address.</div>
			<el-button type="primary" round class='btn' @click="ok" style="margin-top:40px;">完成</el-button>
		</div>
	</div>
</template>

<script>
	import Web3 , { BigNumber } from "web3";
