	import Clipboard from 'clipboard';
	
	export default {
		data() {
			//验证密码
			var validatePass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('Please input a password'));
				} else {
					if (this.ruleForm.checkPass !== '') {
						this.$refs.ruleForm.validateField('checkPass');
					}
					callback();
				}
			};
			//验证重复密码
			var validatePass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('Please enter the password again'));
				} else if (value !== this.ruleForm.pass) {
					callback(new Error('The two passwords are inconsistent!'));
				} else {
					callback();
				}
			};
			var validatehelpword = (rule, value, callback) => {
				if (!value) {
					return callback(new Error('Mnemonic cannot be empty'));
				} else {

				}
			};
			
			var validatehelpPass = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('Please input a password'));
				} else {
					if (this.wordForm.checkPass !== '') {
						this.$refs.wordForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatehelpPass2 = (rule, value, callback) => {
				if (value === '') {
					callback(new Error('Please enter the password again'));
				} else if (value !== this.wordForm.pass) {
					callback(new Error('The two passwords are inconsistent!'));
				} else {
					callback();
				}
			};
			return {
				page:1,
				i: null,
				chooseservers:null,
				web3:null,
				loading:true,
				buttonloading: false,
				walletAddress: null,
				ruleForm: {
					pass: '',
					checkPass: '',
					age: ''
				},
				wordForm: {
					pass: '',
					checkPass: '',
					helpword: ""
				},
				epkForm: {
					word: '',
