					}]
				},

				msg: "dapp",
				userinfo: null,
				serverinfo: [],
				helpword: "",
				qrinfo: {
					type: null,
					url: null,
					epk: null,
				},
				a: 0,
				b: 0,
				bt: 0,
				psd: null,
				buttonloading: false,
				publickey: null,
				privatekey: null,
				publickeyhash: null,
