				}, {
					"inputs": [],
					"name": "getDImCId",
					"outputs": [{
						"internalType": "bytes32[]",
						"name": "",
						"type": "bytes32[]"
					}],
					"stateMutability": "view",
					"type": "function"
				}, {
					"inputs": [{
						"internalType": "bytes32[]",
						"name": "CId",
