				}).then((result) => {
					myContract.methods.getCidInfo(this.publickeyhash).call({
						from: this.walletAddress
					}).then((res) => {
						this.bt = 1;
						loading.close();
						console.log(res)
						this.creatQrCode(res);
					})
				}).catch((error) => {
					loading.close();
					throw error;
				});
			},
			//生成二维码
			creatQrCode(res) {
				console.log('生成二维码')
				this.page +=1;

				function hexCharCodeToStr(hexCharCodeStr) {
					var trimedStr = hexCharCodeStr.trim();
					var rawStr = trimedStr.substr(0, 2).toLowerCase() === "0x" ? trimedStr.substr(2) : trimedStr;
					var len = rawStr.length;
					if (len % 2 !== 0) {
						alert("Illegal Format ASCII Code!");
						return "";
					}
					var curCharCode;
					var resultStr = [];
					for (var i = 0; i < len; i = i + 2) {
						curCharCode = parseInt(rawStr.substr(i, 2), 16); // ASCII Code Value
						resultStr.push(String.fromCharCode(curCharCode));
					}
					let str = "\u0000";
					return resultStr.join("");
				}
				this.qrinfo.type = 0;
				this.qrinfo.epk = ciphertextstr(this.privatekeyaes.slice(2))
				this.qrinfo.url = hexCharCodeToStr(res._serverMark).replace(/\0/g, '')+':1443';

				setTimeout(() => {
					const codeHtml = document.getElementById("qrcode");
					codeHtml.innerHTML = "";
					var table = JSON.stringify(this.qrinfo);
					var qrcode = new QRCode(this.$refs.qrcode, {
						text: table, // 二维码地址
						width: 287,
						height: 287,
						colorDark: "#000000",
						colorLight: "#ffffff",
						correctLevel: 3
					});
				}, 500);
			},
		}
	}
</script>

