import CryptoJS from "crypto-js"

export function hextoString(hex) {
    var arr = hex.split("")
    var out = ""
    for (var i = 0; i < arr.length / 2; i++) {
        var tmp =  arr[i * 2] + arr[i * 2 + 1]
        var charValue = String.fromCharCode(tmp);
        out += charValue
    }
    return out
};
export function HexToBase64  (sha1)  {
	var digits = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/"
	var base64_rep = ""
	var ascv
	var bit_arr = 0
	var bit_num = 0
  
	for (var n = 0; n < sha1.length; ++n) {
	  if (sha1[n] >= 'A' && sha1[n] <= 'Z') {
		ascv = sha1.charCodeAt(n) - 55
	  } else if (sha1[n] >= 'a' && sha1[n] <= 'z') {
		ascv = sha1.charCodeAt(n) - 87
	  } else {
		ascv = sha1.charCodeAt(n) - 48
	  }
  
	  bit_arr = (bit_arr << 4) | ascv
	  bit_num += 4
	  if (bit_num >= 6) {
		bit_num -= 6
  
		base64_rep += digits[bit_arr >>> bit_num]
		bit_arr &= ~ (-1 << bit_num)
	  }
	}
  
	if (bit_num > 0) {
	  bit_arr <<= 6 - bit_num
	  base64_rep += digits[bit_arr]
	}
	var padding = base64_rep.length % 4
  
	if (padding > 0) {
	  for (var n = 0; n < 4 - padding; ++n) {
		base64_rep += "="
	  }
	}
	return base64_rep
  }
//加密
export function encrypt(data,key,iv) {
	//编码 key
	// key = hextoString(key);
	// key = CryptoJS.enc.Utf8.parse(key);
	// key = CryptoJS.enc.Hex.parse(key);
	key = CryptoJS.enc.Hex.parse(key);
 console.log("key==",key);
    //编码iv
	// iv = hextoString(iv);
	// iv = CryptoJS.enc.Utf8.parse(iv);
	iv = CryptoJS.enc.Hex.parse(iv);
 console.log("iv==",iv);
    //编码data
	//data = hextoString(data);
	//data = CryptoJS.enc.Utf8.parse(data);
	data = CryptoJS.enc.Hex.parse(data);
 console.log("data==",data);
 console.log("data.lenth==",data.length);
    // 加密模式为CBC，补码方式为NoPadding
    let encrypted = CryptoJS.AES.encrypt(data, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.NoPadding
    });
    console.log("加密后的数据:",encrypted.ciphertext.toString())
    //返回base64
    return encrypted.ciphertext.toString()
}
//解密
export function decrypt(data,key,iv) {
	console.log("decrypt_data==",data);
    //编码
    key = CryptoJS.enc.Hex.parse(key);
 console.log("key==",key);
    //编码
    iv = CryptoJS.enc.Hex.parse(iv);
 console.log("iv==",iv);
    //Base64编码
     const base64 = CryptoJS.enc.Hex.parse(data);
    const base64Str = CryptoJS.enc.Base64.stringify(base64);
    const decrypt = CryptoJS.AES.decrypt(base64Str, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.NoPadding
    });
    let decryptedStr = decrypt.toString(CryptoJS.enc.Hex);
    console.log("解密后的数据:",decryptedStr)
    return decryptedStr.toString();
}
export function hexparse(str){
    var a=CryptoJS.enc.Hex.parse(str);
	// console.log("a:"+CryptoJS.SHA256(a)) 
	return CryptoJS.SHA256(a);
}
