			},


			msgsd() {
				//比特币的HD钱包
				const bip39 = require('bip39');
				const bitcoin = require('bitcoinjs-lib')
				//     var  Wallet =require('ethereumjs-wallet') 
				// const bip32=require('bip32');
				var util = require('ethereumjs-util')
				const base58 = require("base58");
				let hdkey = require('ethereumjs-wallet');

				let words = bip39.generateMnemonic(128);



				let seedHex = bip39.mnemonicToSeedHex(
				words); //"general replace library street dolphin wheel live own prefer arrange boy then"


				function uint8Array(uint8Array) {
					return Array.prototype.map
						.call(uint8Array, (x) => ('00' + x.toString(16)).slice(-2))
						.join('');
				}
				this.mnemonicwords = words;

				let root = hdkey.hdkey.fromMasterSeed(seedHex);


				let path = "m/44/0'/" + "0" + "'/0/0"

				let child0 = root.derivePath("m/44'/0'/224752737'/0/0");



				//解码BASE58







				this.privatekey = child0._hdkey._privateKey
				var p = this.privatekey;
				// p[0] = p[0]&0xF;
				// p[1] = p[1]&0x8;
				// p[62] = p[62]&0x7;
				// p[63] = p[63]&0xF;
				// p[62] = p[62]|0x4;
				// p[63] = p[63]|0x0;
				// this.privatekey=p;


				this.privatekey[0] &= 248;
				this.privatekey[31] &= 127;
				this.privatekey[31] |= 64;



				let ioio = uint8Array(this.privatekey);
				this.privatekey = ioio;

				var privatekey = this.privatekey;
				var privateprr = allocate(intArrayFromString(privatekey), 'i8', 0);
				var publicptr = allocate(intArrayFromString(privatekey), 'i8', 0);

				_generate_public_key_64(publicptr, privateprr);
				var publickey = Pointer_stringify(publicptr);

				_free(privateprr);
				_free(publicptr);


				this.publickey = publickey;











			},
			pkey(psd) {
				//HD钱包生成密钥对

				this.msgsd();


				var b = this.privatekey;
				this.privatekey = "0x" + this.privatekey;


				//        //对公钥进行SHA256加密
				var c = this.publickey
				this.publickey = "0x" + this.publickey;
				var c1 = hexparse(c);


				//this.publickeyhash=sha256(c1).toUpperCase();

				this.publickeyhash = "0x" + c1;

				//对私钥进行AES加密





				var key = sha256(psd.toString());


				var iv = key.substring(key.length - 32);

				//var b = hextoString(b);

				this.privatekeyaes = encrypt(b, key, iv).toUpperCase();
				this.privatekeyaes = "0x" + this.privatekeyaes;

				var op = decrypt(this.privatekeyaes.slice(2), key, iv);


				this.epkForm.word = this.mnemonicwords;
				this.epkForm.cid = this.publickeyhash;
