
<template>
	<div class='center'>
		<img src="@/assets/small_logo.png" class="logo" alt="">
		<p class="welcome">Light up your rights.</p>
		<div class="create_box">
			<p class='title'>For the first time, start setting up now!</p>
			<p class='content'>A new Metatel account and account mnemonic phrase will be created for you</p>
			<el-button type="primary" class='btn' round @click="create">Create</el-button>
		</div>
		<div class="import_box">
			<p class='title'>No, I already have a mnemonic phrase for my account.</p>
			<p class='content'>Import your existing Metatel using a Secret Recovery Phrase</p>
			<el-button type="primary" class='btn' round @click="imports">Import</el-button>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				name: "HelloWorld",
				flag:false
			};
		},
		created() {
			const web3 = require("web3");
			if (localStorage.getItem("zheng")) {
				var n = localStorage.getItem("zheng");
				this.num = parseInt(n);
			}

			if (window.ethereum) {
				window.ethereum.enable().then((res) => {
					this.walletAddress = res[0];
				});
				this.flag=true;
			} else {
				this.$message.error("Please install MetaMask Wallet");
			}

			this.web3 = new Web3(
				Web3.givenProvider || new Web3.providers.HttpProvider("http://192.168.138.110:8080")
			);
		},
		methods:{
			create() {
				if(this.flag){
					this.$router.push({path:'/create'})
				}else{
					this.$message.error("Please install MetaMask Wallet");
				}
			},
			imports(){
				if(this.flag){
					this.$router.push({path:'/import'})
				}else{
					this.$message.error("Please install MetaMask Wallet");
				}
			},
		}
	}
</script>

<style scoped>
	.welcome{
		margin-top:3vh ;font-weight:bold;font-size:36px ;color:black;
	}

	.create_box{
		margin-top: 7vh;
		font-size: 1vw;
		color: #333;
	}
	
	.import_box{
		margin-top: 5vh;
		font-size: 1vw;
		color: #333;
		padding-bottom: 100px;
	}
	.title{
		font-size: 24px;
	}
	.content{
		font-size: 18px;
	}
	.btn{
		margin-top: 0px;
		width: 14vh;
		/* height: 4vh; */
		/* line-height: 4vh; */
	}
	.testapp{
		padding-top:1.5vh ;color:black;
	}
	.hi{
		padding-top:1.3vh;color:black;
	}
	.begin{
		margin-top:6vh!important;width:200px;
	}
</style>